import React from 'react'
import './GridHome.css'
import {Thumbnail} from './Content'
import {useHistory} from 'react-router-dom'

const GridHome = ({Allcategory,loading}) => {
    const history = useHistory()
    const A = Thumbnail.filter((el)=>el.id > '1')
    return loading ? (<div>loading....</div>
            ):(
                <div className='Grid-Home'>
                    {Allcategory.map((Content)=>
                        <>
                            <div className='img'>
                                    <img src={Content.img} onClick={()=> history.push(`/category/${Content.title}`)}/>
                                </div>
                                <div className='Content-Grid-Home'>
                                    <p>{Content.title}</p>
                                    <h5>{Content.text}</h5>
                                </div>
                        </>
                    )}
                </div>
        )
}

export default GridHome