import {
    GET_CATEGORY_SUCCESS,
    GET_CONTENT_SUCCESS,
    GET_CATEGORY_FAIL,
    GET_CONTENT_FAIL
} from '../action/types';

const initialState = {
    Allcontent: [],
    loading: true,
    error:null,
  };
  
  export default function (state = initialState, action) {
    const { type, payload } = action;
  
    switch (type) {
      case GET_CONTENT_SUCCESS:
        return {
          ...state,
          Allcontent: payload,
          loading: false,
          error: null,
        };
      case GET_CONTENT_FAIL:
          return{
            ...state,
            Allcontent: [],
            loading: false,
            error: payload,
          }    
      default:
        return state;
    }
  }