import React from 'react'
import './Home.css'
import Header from '../components/Header/Header'
import GridHome from '../components/GridHome/GridPrivate'
import HeaderPict from '../img/header.png'
import {useHistory} from 'react-router-dom'
const Private = ({kontol}) => {
     const history = useHistory()
    const handlePushDetail = ()=>{
        history.push("/category")
    }
    console.log(kontol,'from Home')
    return (
        <div className='Home'>
            <div className='Home-pict'>
                <img src={HeaderPict}/>
            </div>
            <Header/>
            <div className='Home-content'>
                <div className='Home-Left'>
                    <div className="Homecontent">
                        <p className='p'>Content</p>
                        <div style={{paddingRight:20}}>
                            <GridHome onClick = {()=>handlePushDetail()}/>
                        </div>
                        
                    </div>  
                </div>
            </div>
            <div className='Home-Foother'>
                
            </div>
        </div>
    )
}

export default Private;
