import React from 'react'
import {data} from '../Data/Fakedata'
import './Detail.css'
const Detail = ({match}) => {
    let A = [...data]
    let filterdata =  A.filter((el)=>el.title === match.params.title );
    return (
        <div className='Detail'>
            {filterdata.map((Content)=>
            <div className='Container'>
            <div style={{width:'auto'}} className='Judul'> 
                <h1>{Content.title}</h1>
            </div>
            <div className='thumbnail'>
                <img src={Content.img}/>
            </div>
            <div className='text'>
              <p style={{textAlign:'justify'}}>{Content.text}</p>
            </div>
            </div>
            )}       
        </div>
    )
}

export default Detail;
