import React,{useState} from 'react';
import './App.css';
import Foother from './components/Foother/Foother'
import Home from './pages/Home'
import Profile from './pages/Profile'
import Navbar from './components/Navbar/Navbar'
import Category from './pages/Category'
import Detail from './pages/Detail'
import ModalLogin from './components/Login/Login'
import ModalRegister from './components/Register/Register'
import Private from './pages/Private'
import EditorText from './pages/EditorText'
import { setAuthToken } from "./config/api";
import { Provider } from "react-redux";
import store from "./redux/store/store";
import AdminRoute from './components/Routing/AdminRoute'
import UserRoute from './components/Routing/UserRoute'


import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

if (localStorage.token) {
  setAuthToken(localStorage.token);
}

function App() {
  const UserName = 'loveyou'
  const Password = 'iwilltrytofixyou'
  const [Lock,setLock]=useState('0')
  const [HandleLogin,setHandleLogin]=useState(false)
  const [HandleRegister,setHandleRegister]=useState(false)

  const Register = () =>{
    setHandleRegister(!HandleRegister)
    setHandleLogin(false)
  }

  const Login = () =>{
    setHandleLogin(!HandleLogin)
    setHandleRegister(false)
  }
  const HandleAdmin = (UName,Pass) =>{
    if(UserName == UName && Password == Pass){
      setLock('1')
    }else{
      setLock('2')
    }
  }
  console.log(Lock,'this is Lock')
  
  return (
    <Provider store={store}>
      <div className="App">
      <Router>
          <Navbar register={Register} login={Login} Lock={Lock}/>
          <div className="page">
            <Route exact path="/bloggerku" component={Home} />
            <AdminRoute exact path="/private" component={Private} />
            <UserRoute exact path="/write" component={EditorText} />
            <UserRoute exact path= "/category/:title" component={Category} />
            <UserRoute exact path= "/about" component={Profile} />
            <UserRoute exact path= "/detail/:title" component={Detail} />
          </div>
        <div className='foother'>
          {HandleLogin && (
            <ModalLogin 
            HandleLogin={HandleLogin} 
            setHandleLogin={setHandleLogin}
            HandleAdmin={HandleAdmin}
            Login={Login}
            />
            )}
            {HandleRegister && (
            <ModalRegister
            HandleRegister={HandleRegister} 
            setHandleRegister={setHandleRegister}
            Register={Register}
            />
            )}
          <Foother/>
        </div>
        </Router>
      </div>
    </Provider>
  );
}

export default App;
