import React,{useState} from 'react'
import { Editor } from 'react-draft-wysiwyg';
import { EditorState } from 'draft-js';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import './EditorText.css'
const EditorText = () => {
  const [editorState, setEditorState] = useState(
    () => EditorState.createEmpty(),
  );
  const [array,setArray]=useState()
    

  const onSubmite = (e) =>{
      e.preventDefault();
      console.log(editorState.blocks)
      setArray(editorState.blocks)
  }
  return (
    <div>
        <div style={{paddingTop:100,paddingBottom:250}}>
        <form onSubmit={(e) => onSubmite(e)}>
          <Editor
            placeholder='Write Your Story Here...'
            initialEditorState={editorState}
            toolbarClassName="toolbarClassName"
            wrapperClassName="wrapperClassName"
            editorClassName="editorClassName"
            onChange={setEditorState}
            />
            <div className='btn-custom'>
               <button>POST</button>
            </div>
        </form>
        </div>
        {array && array.map((Content)=>
                <>
                        <div className='Content-Grid-Home'>
                            <p>{Content.text}</p>
                        </div>
                </>
            )}
    </div>
    
  )
}

export default EditorText
