import { API, setAuthToken } from '../../config/api';

import {
    GET_CATEGORY_SUCCESS,
    GET_CONTENT_SUCCESS,
    GET_CATEGORY_FAIL,
    GET_CONTENT_FAIL
} from './types';

export const getCategory=()=>async(dispatch)=>{
    try {
		let res = await API.get('/category');
		dispatch({
			type: GET_CATEGORY_SUCCESS,
			payload: res.data.data
		});
	} catch (err) {
		dispatch({
			type: GET_CATEGORY_FAIL,
			payload: 'error'
		});
	}
}