import React from 'react'
import './Navbar.css'
import {BrowserRouter as Router,Link} from 'react-router-dom'
import logo from '../../img/logo.png'
import { connect } from "react-redux";
import {handleLogout} from "../../redux/action/auth"
const Navbar = ({handleLogout,register,login,auth: {isAuthenticated,user}}) => {

    const HandleLogout = ()=>{
        handleLogout()
    }
    return (
        <div style={{position:'fixed',width:'100%'}}>
            {user=== null? (
            <>
            <ul>
                <li><Link to='/bloggerku'><img src={logo} style={{width:40}}/></Link></li>
                <li><Link to='/about' style={{paddingTop:30}}>About</Link></li>
                <li><Link to='/lastupdate' style={{paddingTop:30}}>Last Update</Link></li>
                {!isAuthenticated &&<li style={{paddingTop:25,marginInlineEnd:20,float:"right"}}><button onClick={login}>Login</button></li>}
                {!isAuthenticated &&<li style={{paddingTop:25,marginInlineEnd:20,float:"right"}}><button onClick={register}>Register</button></li>}
                {isAuthenticated &&<li style={{paddingTop:25,marginInlineEnd:20,float:"right"}}><button onClick={()=>HandleLogout()}>Logout</button></li>}
                {isAuthenticated &&<li><Link to='/write' style={{paddingTop:30}}>WRITE</Link></li>}
            </ul>
            </>
            ):
            (
            <>
            <ul>
                <li><Link to='/bloggerku'><img src={logo} style={{width:40}}/></Link></li>
                <li><Link to='/about' style={{paddingTop:30}}>About</Link></li>
                <li><Link to='/lastupdate' style={{paddingTop:30}}>Last Update</Link></li>
                {(isAuthenticated && user.role == 1) && <li><Link to='/private' style={{paddingTop:30}}>My-Love</Link></li>}
                {!isAuthenticated &&<li style={{paddingTop:25,marginInlineEnd:20,float:"right"}}><button onClick={login}>Login</button></li>}
                {!isAuthenticated &&<li style={{paddingTop:25,marginInlineEnd:20,float:"right"}}><button onClick={register}>Register</button></li>}
                {isAuthenticated &&<li style={{paddingTop:25,marginInlineEnd:20,float:"right"}}><button onClick={()=>HandleLogout()}>Logout</button></li>}
                {isAuthenticated &&<li><Link to='/write' style={{paddingTop:30}}>WRITE</Link></li>}
            </ul>
            </>
            )
            }
            
        </div>
    )
}

const mapStateToProps = (state) => ({
    auth: state.auth
  });
  
  export default connect(mapStateToProps, {handleLogout})(Navbar);